import React from 'react'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import './socials.css'
import youtube from './img/youtube.svg'
import twitter from './img/twitter.svg'
import instaram from './img/instagram.svg'
import facebook from './img/facebook.svg'
const Socials = () => {
  return (
    <div className='socials'>
      <a href='https://m.facebook.com/illyminiachi/'>
        <img src={facebook} alt='' className='icon' />
      </a>
      <a href='https://mobile.twitter.com/illyminiachi'>
        <img src={twitter} alt='' className='icon' />
      </a>
      <a href='https://m.youtube.com/channel/UC7AqzPQjiIzlr0CqL5XSYLQ'>
        <img src={youtube} alt='' className='icon' />
      </a>
      <a href='https://www.instagram.com/illyminiachi/'>
        <img src={instaram} alt='' className='icon' />
      </a>
    </div>
  )
}

export default Socials
